import { Middleware } from "redux";
import axios from "axios";
import {
  HTTP_REQUEST_FAILURE,
  HTTP_REQUEST,
  HTTP_REQUEST_SUCCESS,
} from "@store/types";

const Observable: Middleware = (store: any) => (next: any) => (action: any) => {
  const { type, payload = {} } = action || {};
  const { credentails, data, method, url, success } = payload;
  const { REACT_APP_API_BASE_URL, NODE_ENV } = process.env;
  const URL = NODE_ENV === "production" ? `${REACT_APP_API_BASE_URL}${url}` : url;
  const xhr: any = { method, url: URL };
  console.log("line#15:credentials:", credentails, data);
  if (credentails) {
    axios.defaults.withCredentials = true;
    xhr.credentials = credentails;
  } else {
    xhr.data = data;
  }

  if (type === HTTP_REQUEST) {
    axios(xhr)
      .then((res) => {
        store.dispatch({
          type: HTTP_REQUEST_SUCCESS,
          payload: res
        });

        if (success) {
          store.dispatch({
            type: success,
            payload: {data, res}
          });
        }
      })
      .catch((err) => {
        store.dispatch({
          type: HTTP_REQUEST_FAILURE,
          payload: err
        });
      });
  }

  return next(action);
};

// Side-effects
export default Observable;
